@charset "utf-8";
.webmain .con { margin-bottom: 45px; }
.webmain .con .title { height: 60px; overflow: hidden; background: url('../../../images/line_contitle.gif') center center repeat-x; margin-bottom: 15px; position: relative; }
.webmain .con .title a.more { display: block; position: absolute; right: 0; top: 16px; background: url('../../../images/more_contitle.gif'); width: 130px; height: 30px; text-indent: -99em; overflow: hidden; }
.webmain .con .title h2 { display: block; width: 165px; height: 60px; background-image: url('../../../images/sprite_committee.jpg'); background-repeat: no-repeat; text-indent: -99em; overflow: hidden; float: left; }
.webmain .con .title ul { float: left; height: 26px; background: #fff; padding: 17px 5px 17px 5px; }
.webmain .con .title ul li { float: left; font-size: 16px; height: 26px; line-height: 26px; overflow: hidden; margin-right: 8px; }
.webmain .con .title ul li a { display: block; width: 100%; height: 26px; line-height: 26px; cursor: pointer; padding: 0 8px; }
.webmain .con .title ul li.c a { background: #000; color: #fff; }

/*导航栏*/
.navTitle { color: #ccc; margin-bottom: 30px; }
.navTitle a { color: inherit }
.navTitle a:hover { color: red }
.navTitle .loginfo { float: right; margin: 0 20px 0 15px; }
.navTitle .loginfo .face { display: inline-block; float: left; width: 20px; height: 20px; background: url("../../../images/icon-logface.jpg") 50% no-repeat; }
.navTitle .loginfo .name { display: inline-block; float: left; height: 20px; margin-left: 15px; line-height: 20px; cursor: pointer; }
.navTitle .loginfo .name::after { content: ""; display: block; position: absolute; margin-left: 55px; margin-top: -10px; border-top: 6px solid #000; border-right: 6px solid #fff; border-left: 6px solid #fff; }

.webmain .topCon .left{float: left;width: 700px; height: 480; }
.webmain .topCon .left img{width: 700px; height: 480px;}
.webmain .topCon .left .text { position: absolute; width: 660px;height: 60px;;margin-top: -100px;padding: 20px 20px 20px 20px;background: rgb(0,0,0); background: rgba(0,0,0,0.6); filter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#57000000,endColorstr=#57000000);}
.webmain .topCon .left .text h3 { color: #fff; font-size: 22px; line-height: 24px; font-weight: normal; margin-bottom: 8px;}
.webmain .topCon .left .text p { color: #fff; font-size: 14px; line-height: 18px; opacity: 0.7;}
.webmain .topCon .left sub {position: absolute; width: 700px; height: 480px; margin-top: -480px; background-color: rgba(0, 0, 0, 0);}
.webmain .topCon .left sub:hover {background-color: rgba(0, 0, 0,.34);}
.webmain .topCon .right{margin-left: 730px;}
.webmain .topCon .right .item{height: 140px; margin-bottom: 30px;}
.webmain .topCon .right .pic{float: left;width: 215px; height: 140px;}
.webmain .topCon .right .pic sub{display: block;z-index: 1;position: absolute; width: 215px; height: 140px; margin-top: -140px; background-color: rgba(0, 0, 0, 0);}
.webmain .topCon .right .pic sub:hover{background-color: rgba(0, 0, 0, .34);}
.webmain .topCon .right .pic img{width: 215px; height: 140px;}
.webmain .topCon .right .pic .text {position: absolute; height: 40px;width: 200px;margin-top: -40px; color: #fff; padding-left: 15px;background-color: rgba(0, 0, 0, 0.6);}
.webmain .topCon .right .pic .text h3{font-size: 14px;}
.webmain .topCon .right .pic .text p{font-size: 12px}
.webmain .topCon .right .content{margin-left: 245px;}
.webmain .topCon .right .content h3{font-size: 16px;font-weight: 300}
.webmain .topCon .right .content p{font-size: 12px; line-height: 30px;}
.webmain .overWatch .group li{width: 57px; height: 26px; text-align: center; line-height: 26px;font-size: 16px;}
.webmain .overWatch .group li.active{color: #fff; background-color: #000;}
.webmain .overWatch .inner{width: 1230px;margin-left: -30px;}
.webmain .overWatch .item{float: left;width: 380px;height: 250px; margin-bottom: 30px;margin-left: 30px;}
.webmain .overWatch .pic{float: left;width: 380px; height: 250px;}
.webmain .overWatch .pic sub{display: block;z-index: 1;position: absolute; width: 380px; height: 250px; margin-top: -250px; background-color: rgba(0, 0, 0, 0);}
.webmain .overWatch .pic sub:hover{background-color: rgba(0, 0, 0, .34);}
.webmain .overWatch .pic img{width: 380px; height: 250px;}
.webmain .overWatch .pic .text {position: absolute; height: 60px;width: 360px;margin-top: -60px; color: #fff; padding: 0 0 0 20px;background-color: rgba(0, 0, 0, 0.6);}
.webmain .overWatch .pic .text h3{font-size: 14px;line-height: 35px;}
.webmain .overWatch .pic .text p{font-size: 12px; line-height: 20px;}
